function isLetterOrDot(e: any) {
  // 
  let char = String.fromCharCode(e.keyCode);
  // 
  const regex = /^[a-zA-Z.]*$/;
  if (regex.test(char)) {
    return true;
  } else {
    // input
    e.preventDefault();
  }
}

