const indicators = [
  { name: "EPS (Diluted) ", rowspan: 2 },
  { name: "Net Margin(%)", rowspan: 2 },
  { name: "ROE() ", rowspan: 2 },
  { name: "IC ", rowspan: 2 },
  { name: "Debt / Equity ", rowspan: 1 },
  { name: "Book Value Per Share ", rowspan: 1 },
  { name: "Free Cash Flow ", rowspan: 1 },
  { name: "Dividend ", rowspan: 2 },
  { name: "Shares (Common) ", rowspan: 1 },
];

// colspan 
const objectSpanMethod = ({
  row,
  column,
  rowIndex,
  columnIndex,
}: SpanMethodProps) => {
  switch (columnIndex) {
    case 0:
      for (const indicator of indicators) {
        if (row.indicator === indicator.name) {
          return {
            rowspan: indicator.rowspan,
            colspan: 1,
          };
        }
      }
      return {
        rowspan: 0,
        colspan: 0,
      };
    case 4:
      const indicatorName = row.indicator.toString();
      switch (indicatorName) {
        case "Net Margin(%)":
        case "ROE() ":
        case "IC ":
          return {
            rowspan: 2,
            colspan: 1,
          };
        case "Dividend ":
          return {
            rowspan: 2,
            colspan: 1,
          };
        default:
          return {
            rowspan: 1,
            colspan: 1,
          };
      }
    default:
      return {
        rowspan: 1,
        colspan: 1,
      };
  }
};

