watch(
  tableData,
  () => {
    calculateTotalScore();
  },
  { deep: true }
);

function calculateTotalScore() {
  totalScore.value =
    divdend1Points.value +
    divdend2Points.value +
    eps1Points.value +
    eps2Points.value +
    sharesPoints.value +
    dePoints.value +
    roe1Points.value +
    roe2Points.value +
    bvpsPoints.value +
    fcfPoints.value +
    net1Points.value +
    net2Points.value +
    ic1Points.value +
    ic2Points.value;
  console.log("totalScore: " + totalScore.value);
  console.log("eps1Points: " + eps1Points.value);
}
