const { toClipboard } = useClipboard();

const isCopied = ref(false);
async function touchCopy() {
  try {
    await toClipboard(stockPrice.value);
    isCopied.value = true;
    setTimeout(() => {
      isCopied.value = false;
    }, 2000);
  } catch (error) {
    console.log(error);
  }
}

let isTotalCopied = ref(false);
async function touchCopyTotal() {
  try {
    await toClipboard(JSON.stringify(totalScore.value));
    isTotalCopied.value = true;
    setTimeout(() => {
      isTotalCopied.value = false;
    }, 2000);
  } catch (error) {
    console.log(error);
  }
}

// reset
function reset() {
  tableData.value.forEach((row) => {
    row.giveScore = false;
    row.noScore = false;
  });

  divdend1Points.value = 0;
  divdend2Points.value = 0;
  eps1Points.value = 0;
  eps2Points.value = 0;
  sharesPoints.value = 0;
  dePoints.value = 0;
  roe1Points.value = 0;
  roe2Points.value = 0;
  bvpsPoints.value = 0;
  fcfPoints.value = 0;
  net1Points.value = 0;
  net2Points.value = 0;
  ic1Points.value = 0;
  ic2Points.value = 0;

  // isNoDivdend.value = false;
}

// csv
function exportToCSV() {
  const csvData = tableData.value.map((row) => {
    return {
      : row.indicator,
      : row.standard,
      : row.scoreStandard,
      : row.giveScore ? "" : row.noScore ? "" : "",
    };
  });
  csvData.push({
    : "",
    : "",
    : totalScore.value.toString(),
    : "",
  });
  const hasNullScore = csvData.some((row) => {
    return row. === null;
  });

  if (!stock.value) {
    alert("");
    return;
  }
  if (hasNullScore) {
    alert("CSV");
    return;
  }
  const csv = Papa.unparse(csvData, {
    encoding: "utf-8",
    quotes: true,
    quoteChar: '"',
    escapeChar: '"',
    delimiter: ",",
    header: true,
    newline: "\r\n",
    skipEmptyLines: true,
  });
  const blob = new Blob([new Uint8Array([0xef, 0xbb, 0xbf]), csv], {
    type: "text/csv;charset=utf-8;",
  });
  const url = window.URL.createObjectURL(blob);
  const link = document.createElement("a");
  link.href = url;
  // Get the current date
  const currentDate = new Date();
  const year = currentDate.getFullYear();
  const month = String(currentDate.getMonth() + 1).padStart(2, "0");
  const day = String(currentDate.getDate()).padStart(2, "0");
  // Construct the filename with the current date
  const filename = `${year}${month}${day}_${stock.value}_.csv`;
  link.download = filename;
  link.click();
  window.URL.revokeObjectURL(url);
}
