              id="confidence_1" />
            <label class="form-check-label" for="confidence_1"></label>
          </div>
          <div class="form-check form-check-inline">
            <input class="form-check-input" v-model="form.confidence_2" type="checkbox" id="confidence_2"
              @click="confidenceNoGivePoint()" />
            <label class="form-check-label" for="confidence_2"></label>
          </div>
        </td>
      </tr>
      <tr>
