<script setup>
import { reactive, ref } from "@vue/reactivity";
import { nextTick } from "vue";
import { watch } from "@vue/runtime-core";
import { useVuelidate } from "@vuelidate/core";
import { required } from "@vuelidate/validators";
import useClipboard from "vue-clipboard3";
import Modal from "./Modal.vue";
import Papa from "papaparse";

const closeableModal = ref(false);
const errorModal = ref(false);

let form = reactive({
  divdend1_1: "",
  divdend1_2: "",
  divdend2_1: "",
  divdend2_2: "",
  divdend3_1: "",
  divdend3_2: "",
  eps_1: "",
  eps_2: "",
  shares_1: "",
  shares_2: "",
  roa_1: "",
  roa_2: "",
  bvps_1: "",
  bvps_2: "",
  fcf_1: "",
  fcf_2: "",
  ic1_1: "",
  ic1_2: "",
  ic2_1: "",
  ic2_2: "",
  iir_1: "",
  iir_2: "",
  total: 0,
});

let divdend1Points = ref(0);
let divdend2Points = ref(0);
let divdend3Points = ref(0);
let epsPoints = ref(0);
let sharesPoints = ref(0);
let roaPoints = ref(0);
let bvpsPoints = ref(0);
let fcfPoints = ref(0);
let ic1Points = ref(0);
let ic2Points = ref(0);
let iirPoints = ref(0);
let total = ref(0);

// 
const netInterestIncome = ref();
const totalRevenue = ref();
const ratio = ref("");

// Divdend
const isNoDivdend = ref(false);
function noDivdends() {
  divdend1Points.value = 0;
  divdend2Points.value = 0;
  divdend3Points.value = 0;

  form.divdend1_1 = false;
  form.divdend2_1 = false;
  form.divdend3_1 = false;

  if (!form.divdend1_2 || !form.divdend2_2 || !form.divdend3_2) {
    form.divdend1_2 = true;
    form.divdend2_2 = true;
    form.divdend3_2 = true;
    isNoDivdend.value = true;
  } else {
    form.divdend1_2 = false;
    form.divdend2_2 = false;
    form.divdend3_2 = false;
    isNoDivdend.value = false;
  }
}

let isShowDividendUrl = ref(false);
watch([() => form.divdend1_1, () => form.divdend2_1], (newValues) => {
  if (newValues.some((value) => value)) {
    isNoDivdend.value = false;
    isShowDividendUrl.value = true;
  } else {
    isShowDividendUrl.value = false;
  }
});

watch(
  [() => form.divdend1_1, () => form.divdend2_1, () => form.divdend3_1],
  (newValues) => {
    console.log(newValues);
    if (newValues.every((value) => value === false)) {
      isNoDivdend.value = true;
      isShowDividendUrl.value = false;
    }
  }
);

watch(
  () => form.divdend1_1,
  (newValue) => {
    if (newValue) {
      form.divdend2_2 = true;
      form.divdend2_1 = false;
      divdend2Points.value = 0;
    }
  }
);

watch(
  () => form.divdend2_1,
  (newValue) => {
    if (newValue) {
      form.divdend1_2 = true;
      form.divdend1_1 = false;
      divdend1Points.value = 0;
    }
  }
);

// IC
watch(
  () => form.ic1_1,
  (newValue) => {
    if (newValue) {
      form.ic2_2 = true;
      form.ic2_1 = false;
      ic2Points.value = 0;
    }
  }
);
watch(
  () => form.ic2_1,
  (newValue) => {
    if (newValue) {
      form.ic1_2 = true;
      form.ic1_1 = false;
      ic1Points.value = 0;
    }
  }
);

// 
function calculate() {
  const referenceValue = 50;
  const value = ((netInterestIncome.value / totalRevenue.value) * 100).toFixed(
    2
  );
  ratio.value = `${value} %`;
  if (value > referenceValue) {
    iirPoints.value = 1;
    form.iir_1 = true;
    form.iir_2 = false;
  } else {
    iirPoints.value = 0;
    form.iir_1 = false;
    form.iir_2 = true;
  }
}

// Divdend
// #region
// 10
function divdend1GetPoint() {
  form.divdend1_2 = false;
  divdend1Points.value = 0.5;
  if (form.divdend1_1 == true) {
    divdend1Points.value = 0;
  }
}

function divdend1NotGetPoint() {
  form.divdend1_1 = false;
  divdend1Points.value = 0;
}
// 10
function divdend2GetPoint() {
  form.divdend2_2 = false;
  divdend2Points.value = 1;
  if (form.divdend2_1 == true) {
    divdend2Points.value = 0;
  }
}

function divdend2NotGetPoint() {
  form.divdend2_1 = false;
  divdend2Points.value = 0;
}

function divdend3GetPoint() {
  form.divdend3_2 = false;
  divdend3Points.value = 0.5;
  if (form.divdend3_1 == true) {
    divdend3Points.value = 0;
  }
}

function divdend3NotGetPoint() {
  form.divdend3_1 = false;
  divdend3Points.value = 0;
}
// #endregion

// EPS
// #region
function epsGetPoint() {
  form.eps_2 = false;
  epsPoints.value = 1;
  if (form.eps_1 == true) {
    epsPoints.value = 0;
  }
}

function epsNotGetPoint() {
  form.eps_1 = false;
  epsPoints.value = 0;
}
// #endregion

// Shares
// #region
function sharesGetPoint() {
  form.shares_2 = false;
  sharesPoints.value = 1;
  if (form.shares_1 == true) {
    sharesPoints.value = 0;
  }
}

function sharesNotGetPoint() {
  form.shares_1 = false;
  sharesPoints.value = 0;
}
// #endregion

// ROA
// #region
function roaGetPoint() {
  form.roa_2 = false;
  roaPoints.value = 1;
  if (form.roa_1 == true) {
    roaPoints.value = 0;
  }
}

function roaNotGetPoint() {
  form.roa_1 = false;
  roaPoints.value = 0;
