function edit() {
  isEdit.value = false;
  isCopied.value = false;
  stockName.value = "";
  stockPrice.value = "";
  isCallLimitReached.value = false;
  reset();
  nextTick(() => {
    stockField.value.focus();
    stockField.value.setSelectionRange(0, stock.value.length);
  });
}

function isLetterOrDot(e) {
  // 
  let char = String.fromCharCode(e.keyCode);
  // 
  const regex = /^[a-zA-Z.]*$/;
  if (regex.test(char)) {
    return true;
  } else {
    // input
    e.preventDefault();
  }
}

