// csv
function exportToCSV() {
  const data = [
    {
      : "Divdend ",
      : "10",
      : 0.5,
      :
        form.divdend1_1 == "" && form.divdend1_2 == ""
          ? null
          : `${form.divdend1_1 ? "" : ""}`,
    },
    {
      : "",
      : "10",
      : 1,
      :
        form.divdend2_1 == "" && form.divdend2_2 == ""
          ? null
          : `${form.divdend2_1 ? "" : ""}`,
    },
    {
      : "",
      : "5>10",
      : 0.5,
      :
        form.divdend3_1 == "" && form.divdend3_2 == ""
          ? null
          : `${form.divdend3_1 ? "" : ""}`,
    },

    {
      : "EPS ",
      : "10",
      : 1,
      :
        form.eps_1 == "" && form.eps_2 == ""
          ? null
          : `${form.eps_1 ? "" : ""}`,
    },
    {
      : "Shares ",
      : "10",
      : 1,
      :
        form.shares_1 == "" && form.shares_2 == ""
          ? null
          : `${form.shares_1 ? "" : ""}`,
    },
    {
      : "ROA() ",
      : "10 ROA > 1.2",
      : 1,
      :
        form.roa_1 == "" && form.roa_2 == ""
          ? null
          : `${form.roa_1 ? "" : ""}`,
    },
    {
      : "Book Value Per Share ",
      : "10",
      : 1,
      :
        form.bvps_1 == "" && form.bvps_2 == ""
          ? null
          : `${form.bvps_1 ? "" : ""}`,
    },
    {
      : "FCF ",
      : "10",
      : 1,
      :
        form.fcf_1 == "" && form.fcf_2 == ""
          ? null
          : `${form.fcf_1 ? "" : ""}`,
    },
    {
      : "IC ",
      : "IC > 10  -",
      : 1,
      :
        form.ic1_1 == "" && form.ic1_2 == ""
          ? null
          : `${form.ic1_1 ? "" : ""}`,
    },
    {
      : "",
      : "IC > 5",
      : 0.5,
      :
        form.ic2_1 == "" && form.ic2_2 == ""
          ? null
          : `${form.ic2_1 ? "" : ""}`,
    },
    {
      : "Interest Income Ratio ",
      : "(Net Interest Income)(Total Revenue)  50%",
      : 1,
      :
        form.iir_1 == "" && form.iir_2 == ""
          ? null
          : `${form.iir_1 ? "" : ""}`,
    },
    { : "", : "", : "", : total.value },
  ];
  const hasNullScore = data.some((row) => {
    return row. === null;
  });

  if (!stock.value) {
    alert("");
    return;
  }
  if (hasNullScore) {
    alert("CSV");
    return;
  }
  const csv = Papa.unparse(data, {
    encoding: "utf-8",
    quotes: true,
    quoteChar: '"',
    escapeChar: '"',
    delimiter: ",",
    header: true,
    newline: "\r\n",
    skipEmptyLines: true,
  });
  const blob = new Blob([new Uint8Array([0xef, 0xbb, 0xbf]), csv], {
    type: "text/csv;charset=utf-8;",
  });
  const url = window.URL.createObjectURL(blob);
  const link = document.createElement("a");
  link.href = url;
  // Get the current date
  const currentDate = new Date();
  const year = currentDate.getFullYear();
  const month = String(currentDate.getMonth() + 1).padStart(2, "0");
  const day = String(currentDate.getDate()).padStart(2, "0");
  // Construct the filename with the current date
  const filename = `${year}${month}${day}_${stock.value}_.csv`;
  link.download = filename;
  link.click();
  window.URL.revokeObjectURL(url);
}
